<template>
  <div id="table">
  <el-table
    :data="tableData"
    style="width: 100%"
    :default-sort = "{prop: 'time', order: 'descending'}"
    empty-text="No Entry">
    <el-table-column
      prop="name"
      label="CLIENT"
      sortable
      width="180">
    </el-table-column>
    <el-table-column
      prop="last_visit_time"
      label="UPDATED"
      sortable
      width="180">
    </el-table-column>
    <el-table-column
      prop="entry"
      label="ENTRIES"
      sortable
      width="140"
      class-name="entry">
    </el-table-column>
    <el-table-column
      label="MOOD"
      :formatter="formatter">
      <template slot-scope="scope">
        <img src="@/assets/emoji/emoji_verygood.png" v-if="scope.row.mood === 5">
        <img src="@/assets/emoji/emoji_good.png" v-if="scope.row.mood === 4">
        <img src="@/assets/emoji/emoji_neutral.png" v-if="scope.row.mood === 3">
        <img src="@/assets/emoji/emoji_bad.png" v-if="scope.row.mood === 2">
        <img src="@/assets/emoji/emoji_verybad.png" v-if="scope.row.mood === 1">
      </template>
    </el-table-column>
    <el-table-column
      prop="alert_num"
      label="ALERTS"
      sortable
      width="100">
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        tableData: [],
      }
    },
    methods: {
      formatter(row, column) {
        return row.address;
      }
    },
    mounted() {
      var get_path ='http://35.197.160.94:8094/doctor/overview?uid_doctor=' + this.$store.state.user
      axios.get(get_path)
              //then获取成功；response成功后的返回值（对象）
      .then(response=>{
          this.patient=response.data;
          for( var index in this.patient) {
            this.patient[index].name = this.patient[index].firstName+' '+this.patient[index].lastName
            this.tableData.push(this.patient[index]); 
          }
          console.log(this.tableData)
      })
              //获取失败
      .catch(error=>{
          console.log(error);
          alert('网络错误，不能访问');
      })
    },
  }
</script>

<style>
  .entry {
    text-align: center !important;
  }
  .el-table img {
    height: 24px;
  }
  .el-table__body tr,
    .el-table__body td {
      padding: 0;
      height: 60px;
  }
#table {
}
</style>


